






































import { Component, Vue } from 'vue-property-decorator';
import { Toast } from 'vant';

@Component({
  name: 'Success',
  components: {
  },
})
export default class Success extends Vue {
  userList: any = [];

  loading = false;

  created() {
    this.getUsers();
  }

  async getUsers() {
    Toast.loading({ forbidClick: true, overlay: true });
    try {
      const data = await this.$xhr.api.get('/lottery2023/users');
      this.userList = data;
    } catch (error) {
      this.$toast('获取用户列表失败');
    } finally {
      Toast.clear();
    }
  }

  // 清除用户数据
  async clearUsers() {
    if (!window.confirm('确定要清除所有用户数据吗？')) {
      return;
    }

    Toast.loading({ forbidClick: true, overlay: true });
    try {
      await this.$xhr.api.post('/lottery2023/users/remove');
      this.userList = [];
    } catch (error) {
      this.$toast('清除用户数据失败');
    } finally {
      Toast.clear();
    }
  }

  getAwardStatusText(awardStatus: string, awardId: number) {
    // awardId 小于0 没中奖, awardStatus -1 没抽奖  0 未领奖 1 已领奖，帮我根据这两个字段判断一下
    if (awardId < 0) {
      return '未中奖';
    }
    if (awardStatus == '-1') {
      return '未抽奖';
    }
    if (awardStatus == '0') {
      return '未领奖';
    }
    if (awardStatus == '1') {
      return '已领奖';
    }
    return '未知';
  }
}
