








































import { Component, Vue } from 'vue-property-decorator';
import { Toast } from 'vant';

@Component({
  name: 'Success',
  components: {
  },
})
export default class Success extends Vue {
  awardsList: any = [];

  stepper = 1;

  loading = false;

  created() {
    this.getAwards();
  }

  async getAwards() {
    Toast.loading({ forbidClick: true, overlay: true });
    try {
      const data = await this.$xhr.api.get('/lottery2023/awards');
      this.awardsList = data;
    } catch (error) {
      this.$toast('获取奖品列表失败');
    } finally {
      Toast.clear();
    }
  }

  async onChange(e: number, x: any) {
    const currentAward = this.awardsList.find((item: any) => item.id === x.name);
    if (currentAward.stock === e || currentAward.quantity < e) return;
    Toast.loading({ forbidClick: true, overlay: true });
    this.loading = true;
    try {
      await this.$xhr.api.post('/lottery2023/changeAawardStock', {
        params: {
          id: x.name,
          num: e,
        },
      });
      currentAward.stock = e;
    } catch (error) {
      this.$toast('获取奖品列表失败');
    } finally {
      this.loading = false;
      Toast.clear();
    }
  }

  readAllUser() {
    this.$router.push({ name: 'user' });
  }
}
